    color: black;
  
    
  
  
  }
  
  h2 {
    /* color: aqua; */
    text-align: center;
    font-size: 5rem;
    margin-bottom: 2rem;
    color: black;
  
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  
  }
  
  .container {
    /* background-color: wheat; */
    max-width: 1300px;
    margin: auto;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  
  }
  
  .box {
    /* background-color: brown; */
    width: 28%;
    margin: 10px;
    padding: 20px;
    box-shadow: 2px 2px 10px gray;
  
  
  }
  
  h5 {
    /* color: aquamarine; */
    color: black;
  
    text-align: center;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  
  }
  
  p {
    color: black;
    font-size: 0.9rem;
  }
  
  img-container {
    width: 100%;
    height: 40%;
    object-fit: contain;
  }
  
  img {
    width: 100%;
    height: 100%;
  }
  
  .content > button{
    color: black;
    margin-top: 1rem;
    width: 5rem;
    text-align: center;
    height: 2rem;
    border-radius: 0.4rem;
  }
   
  
   