.footer {
    display: flex;
    flex-direction: row;
    gap: 30px;
    padding-top: 50px;
    padding-bottom: 50px;
    margin-left: 195px;
    margin-right: 195px;
  }
  
  .company,
  .features {
    width: 148px;
    height: 170px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .companyList,
  .legalList,
  .featuresList,
  .resourcesList {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-direction: column;
  }
  
  .company > h5,
  .legal > h5,
  .features > h5,
  .resources > h5,
  .inTouch > h5 {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.1px;
    color: #252b42;
  }
  
  .companyList > li,
  .legalList > li,
  .featuresList > li,
  .resourcesList > li {
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.2px;
    margin-bottom: 10px;
    color: #737373;
  }
  
  .legal,
  .resources {
    width: 152px;
    height: 170px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .inTouch {
    width: 321px;
    height: 131px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .inTouch > div {
    width: 321px;
    height: 87px;
  }
  
  .email {
    width: 321px;
    height: 58px;
  }
  
  .email > input {
    width: 204px;
    height: 58px;
    background: #f9f9f9;
    border: 1px solid #e6e6e6;
    border-radius: 5px;
    padding: 15px 0px 15px 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 28px;
    letter-spacing: 0.2px;
    color: #737373;
  }
  
  .input-text {
    height: 28px;
    font-weight: 400;
    font-size: 12px;
    line-height: 28px;
    letter-spacing: 0.2px;
    color: #737373;
  }
  
  .email > button {
    width: 117px;
    height: 58px;
    background: #23a6f0;
    border: 1px solid #e6e6e6;
    border-radius: 0px 5px 5px 0px;
  }

  .mwl-container {
    width: 1440px;
    height: 74px;
    background: #fafafa;
  }
  
  .mwl {
    width: 1050px;
    height: 74px;
    margin: 0 195px;
    display: flex;
    align-items: center;
  }
  
  .mwl > h6 {
    font-family: "Montserrat";
    font-weight: 700;
    font-size: 14px;
    letter-spacing: 0.2px;
    color: #737373;
  }